import React, { useState } from 'react';
import { Group, Image, Rect, Text } from 'react-konva';
import { Html } from 'react-konva-utils';
import useImage from 'use-image';
import { v4 as uuidv4 } from 'uuid';

import { useCoverContext, useSizesContext } from 'contexts';
import { useDragAndDrop } from 'conva/components';
import { CoverImage, PosTypes, RenderDir } from 'types';
import { BandDirPopover } from 'uicomponents';

interface CoverImageProps {
  albumCover: CoverImage;
}

export const AlbumCover: React.FC<CoverImageProps> = ({ albumCover }) => {
  const { id, link, x, y, isDragging, artist, album, dir } = albumCover;
  const [image] = useImage(link);
  const [textDirOpen, setTextDirOpen] = useState(false);
  const {
    erase,
    setCover,
    points,
    setPoints,
    showTitles,
    editLines,
    setLines,
  } = useCoverContext();
  const { coverSize, fontSize } = useSizesContext();
  const { handleDragStart, handleDragEnd, handleDragBound } = useDragAndDrop();

  const handleEraseImage = (id: string) => {
    if (erase) {
      setCover((currentCover) => currentCover.filter((c) => c.id !== id));
      return;
    }
  };

  const handleDrawLine = (id: string, pos: PosTypes) => {
    if (!points) {
      setPoints({ id, pos });
    } else if (points.id !== id) {
      setLines((currentLines) => {
        const lineCopy = [...currentLines];

        const foundLine = lineCopy.find(
          (currentLine) => currentLine.target.id === id,
        );

        if (foundLine) {
          foundLine.target = { id, pos };
          return lineCopy;
        }

        return [
          ...lineCopy,
          {
            label: {
              text: '',
              dir: RenderDir.DOWN,
            },
            origin: { ...points },
            target: { id, pos },
            id: uuidv4(),
          },
        ];
      });
      setPoints(null);
    }
  };

  const handleChangeDir = (dir: RenderDir) => {
    setCover((currentCover) =>
      currentCover.map((star) => ({
        ...star,
        dir: star.id === id ? dir : star.dir,
      })),
    );
  };

  return (
    <Group
      onDragStart={handleDragStart}
      onDragEnd={handleDragEnd}
      dragBoundFunc={handleDragBound}
      draggable
      x={x}
      y={y}
      id={id}
      scaleX={isDragging ? 1.1 : 1}
      scaleY={isDragging ? 1.1 : 1}>
      <Group>
        {showTitles && (
          <Group onClick={() => setTextDirOpen(true)}>
            <Text
              fontSize={fontSize}
              text={artist + '\n\n' + album}
              x={-coverSize / 2}
              y={
                dir === RenderDir.DOWN
                  ? coverSize + fontSize
                  : -coverSize - fontSize
              }
              align="center"
              verticalAlign={dir === RenderDir.DOWN ? 'top' : 'bottom'}
              fill="yellow"
              width={coverSize * 2}
              height={coverSize}
            />
            <Html>
              <BandDirPopover
                open={!!textDirOpen}
                onClose={() => setTextDirOpen(false)}
                onSubmit={handleChangeDir}
                defaultDir={dir ?? RenderDir.DOWN}
              />
            </Html>
          </Group>
        )}
        {editLines && (
          <>
            <Rect
              x={0 + coverSize / 2}
              y={0 - coverSize / 4 - coverSize / 8}
              width={coverSize / 2}
              height={coverSize / 2}
              fill="white"
              rotation={45}
              opacity={0.05}
              onClick={() => handleDrawLine(id, PosTypes.TOP)}
            />
            <Rect
              x={0 + coverSize}
              y={0 + coverSize / 8}
              width={coverSize / 2}
              height={coverSize / 2}
              fill="white"
              rotation={45}
              opacity={0.05}
              onClick={() => handleDrawLine(id, PosTypes.RIGHT)}
            />
            <Rect
              x={0}
              y={0 + coverSize / 8}
              width={coverSize / 2}
              height={coverSize / 2}
              fill="white"
              rotation={45}
              opacity={0.05}
              onClick={() => handleDrawLine(id, PosTypes.LEFT)}
            />
            <Rect
              x={0 + coverSize / 2}
              y={0 + coverSize - coverSize / 4 - coverSize / 8}
              width={coverSize / 2}
              height={coverSize / 2}
              fill="white"
              rotation={45}
              opacity={0.05}
              onClick={() => handleDrawLine(id, PosTypes.BOTTOM)}
            />
          </>
        )}
      </Group>
      <Image
        image={image}
        width={coverSize}
        height={coverSize}
        onClick={() => handleEraseImage(id)}
      />
    </Group>
  );
};
