import { useCoverContext, useSizesContext } from 'contexts';
import { KonvaEventObject } from 'konva/lib/Node';
import { Vector2d } from 'konva/lib/types';

export const useDragAndDrop = () => {
  const { setCover } = useCoverContext();
  const { coverSize, dragLimits } = useSizesContext();

  const handleDragBound = (pos: Vector2d) => {
    const newX = Math.max(
      dragLimits.x,
      Math.min(pos.x, dragLimits.width - coverSize / 2),
    );
    const newY = Math.max(
      dragLimits.y,
      Math.min(pos.y, dragLimits.height - coverSize / 2),
    );

    return {
      x: newX,
      y: newY,
    };
  };

  const handleDragStart = (e: KonvaEventObject<DragEvent>) => {
    setCover((currentCover) =>
      currentCover.map((star) => {
        return {
          ...star,
          isDragging: star.id === e.target.id(),
        };
      }),
    );
  };

  const handleDragEnd = (e: KonvaEventObject<DragEvent>) => {
    setCover((currentCover) =>
      currentCover.map((star) => {
        return star.isDragging
          ? {
              ...star,
              isDragging: false,
              x: e.target.x(),
              y: e.target.y(),
            }
          : star;
      }),
    );
  };

  return { handleDragStart, handleDragEnd, handleDragBound };
};
