export interface DragLimits {
  x: number;
  y: number;
  width: number;
  height: number;
}

export enum RenderDir {
  UP = 'up',
  DOWN = 'down',
  LEFT = 'left',
  RIGHT = 'right',
}

export interface Point {
  id: string;
  pos: PosTypes;
}

export interface LinePoint {
  id: string;
  origin: Point;
  target: Point;
  label: {
    text: string;
    dir: RenderDir;
  };
}

export interface ApiKey {
  DiscogsKey: string;
  DiscogsSecret: string;
}

export interface CoverImage {
  id: string;
  link: string;
  x: number;
  y: number;
  width: number;
  height: number;
  isDragging: boolean;
  artist: string;
  album: string;
  dir: RenderDir;
}

export enum PosTypes {
  LEFT = 'left',
  RIGHT = 'right',
  TOP = 'top',
  BOTTOM = 'bottom',
}

export enum PopupState {
  ARTIST = 'artist',
  ALBUM = 'album',
}

export interface SearchParams {
  [PopupState.ARTIST]: string;
  [PopupState.ALBUM]: string;
}
