import React, { useMemo } from 'react';
import { Arrow, Circle, Group, Rect, Text } from 'react-konva';

import { useCoverContext, useSizesContext } from 'contexts';
import { LinePoint, PosTypes, RenderDir } from 'types';

interface LineProps {
  line: LinePoint;
  handleOpen: (line: LinePoint) => void;
}

const convertPosToXY = (coverSize: number, pos: PosTypes) => {
  if (pos === PosTypes.TOP) {
    return {
      x: coverSize / 2,
      y: -coverSize / 16,
    };
  } else if (pos === PosTypes.BOTTOM) {
    return {
      x: coverSize / 2,
      y: coverSize + coverSize / 16,
    };
  } else if (pos === PosTypes.LEFT) {
    return {
      x: -coverSize / 16,
      y: coverSize / 2,
    };
  } else {
    return {
      x: coverSize + coverSize / 16,
      y: coverSize / 2,
    };
  }
};

interface LineParams {
  x: number;
  y: number;
  align: string;
  midX: number;
  midY: number;
  points: number[];
}

export const DrawLine: React.FC<LineProps> = (props) => {
  const { line, handleOpen } = props;
  const { fontSize, coverSize } = useSizesContext();
  const { showBalls, cover } = useCoverContext();

  const lineParams = useMemo((): LineParams | undefined => {
    const { origin, target } = line;

    if (target) {
      const originSquare = cover.find((cov) => cov.id === line.origin.id);
      const targetSquare = cover.find((cov) => cov.id === line.target?.id);

      if (originSquare && targetSquare) {
        const originPos = convertPosToXY(coverSize, origin.pos);
        const targetPos = convertPosToXY(coverSize, target.pos);

        const points = [
          originSquare.x + originPos.x,
          originSquare.y + originPos.y,
          targetSquare.x + targetPos.x,
          targetSquare.y + targetPos.y,
        ];

        const midX = (points[0] + points[2]) / 2;
        const midY = (points[1] + points[3]) / 2;

        const textPost = {
          x: midX,
          y: midY,
          align: 'center',
          midX,
          midY,
          points,
        };

        if (line.label.dir === RenderDir.DOWN) {
          textPost.y = midY + fontSize * 1.5;
          textPost.x = midX - coverSize;
          textPost.align = 'center';
        } else if (line.label.dir === RenderDir.UP) {
          textPost.y = midY - fontSize * 3;
          textPost.x = midX - coverSize;
          textPost.align = 'center';
        } else if (line.label.dir === RenderDir.LEFT) {
          textPost.x = midX - fontSize * 1.5 - coverSize * 2;
          textPost.y = midY - fontSize / 2;
          textPost.align = 'right';
        } else if (line.label.dir === RenderDir.RIGHT) {
          textPost.x = midX + fontSize * 1.5;
          textPost.y = midY - fontSize / 2;
          textPost.align = 'left';
        }

        return textPost;
      }
    }
  }, [cover, coverSize, fontSize, line]);

  if (!lineParams) return null;

  return (
    <>
      <Arrow
        points={lineParams.points}
        stroke="yellow"
        strokeWidth={fontSize / 4}
        pointerLength={fontSize}
      />
      {showBalls && (
        <Circle
          x={lineParams.midX}
          y={lineParams.midY}
          radius={fontSize / 1.5}
          fill="yellow"
          onClick={() => handleOpen(line)}
        />
      )}
      {line.label.text && (
        <Group>
          <Rect
            x={lineParams.x}
            y={lineParams.y}
            width={coverSize * 2}
            height={fontSize}
            fill="#282c34"
          />
          <Text
            x={lineParams.x}
            y={lineParams.y}
            width={coverSize * 2}
            align={lineParams.align}
            text={line.label.text}
            fontSize={fontSize}
            fill="yellow"
            onClick={() => handleOpen(line)}
          />
        </Group>
      )}
    </>
  );
};
