import { useState, useCallback } from 'react';
import { Html } from 'react-konva-utils';

import { useCoverContext } from 'contexts';
import { EditLabelPopover } from 'uicomponents';
import { LinePoint, RenderDir } from 'types';
import { DrawLine } from '.';

interface TextEdit {
  defaultText: string;
  defaultDir: RenderDir;
  id: LinePoint['id'];
}

export const DrawLines: React.FC = () => {
  const { erase, lines, setLines } = useCoverContext();
  const [textEdit, setTextEdit] = useState<TextEdit | null>(null);

  const handleSubmit = (text: string, dir: RenderDir) => {
    setLines((currentlines) => {
      const lineCopy = [...currentlines];

      if (textEdit) {
        const foundLine = lineCopy.find(
          (currentLine) => currentLine.id === textEdit.id,
        );

        if (foundLine) {
          foundLine.label.text = text;
          foundLine.label.dir = dir;
        }
      }
      return lineCopy;
    });
    setTextEdit(null);
  };

  const handleOpen = useCallback(
    (line: LinePoint) => {
      if (erase) {
        setLines((currentlines) =>
          currentlines.filter((currentLine) => !(currentLine.id === line.id)),
        );
        return;
      }

      setTextEdit({
        id: line.id,
        defaultText: line.label.text,
        defaultDir: line.label.dir,
      });
    },
    [erase, setLines],
  );

  return (
    <>
      <Html>
        <EditLabelPopover
          open={!!textEdit}
          onClose={() => setTextEdit(null)}
          onSubmit={handleSubmit}
          defaultText={textEdit?.defaultText ?? ''}
          defaultDir={textEdit?.defaultDir ?? RenderDir.DOWN}
        />
      </Html>
      {lines.map((lineParam, index) => (
        <DrawLine key={index} line={lineParam} handleOpen={handleOpen} />
      ))}
    </>
  );
};
export { RenderDir };
