import './App.css';
import { useApiContext } from 'contexts';
import { ApiInputPopover } from 'uicomponents';
import { CoverBoard } from 'conva/CoverBoard';

function App() {
  const { apiKey } = useApiContext();

  if (apiKey.DiscogsKey && apiKey.DiscogsSecret) {
    return (
      <div className="App">
        <CoverBoard />
      </div>
    );
  }
  return (
    <div className="App">
      <ApiInputPopover open />
    </div>
  );
}
export default App;
